    margin-right: auto;
    //background-color: #eeeeee;
}
.resetButton{
    background-color: skyblue;
    padding: 5px 10px 5px 10px;
    border: 1px solid #5980d4;
    color: grey;
    width: 100px;
   // height: 40px;
    border-radius: 6px;
    display: block;
