}
.arrowNoticebody{
    position: relative;
    left:45px;
    display: inline-block;
    //width: 95px;
    //height: 30px;
    background-color: #ffffff;
    border: 1px solid #c4ccdd;
    border-radius: 5px;
    padding: 10px 25px 10px 25px;
    box-shadow: 2px 2px 5px #c4ccdd;
}
.arrowNoticebody:hover{
    box-shadow: 2px 2px 5px #9da1aa;
}
//$w:10px;
@mixin arrowNoticearrow($color,$w){
    display: inline-block;
    width: 0px;
    height: 0px;
    //background-color:$color;
    border-top: $w solid transparent;
    border-left: $w solid transparent;
    border-bottom: $w solid transparent;
    border-right: $w solid $color;
}
.arrowNoticearrowOuter{
    position: absolute;
    left:-20px;
    @include arrowNoticearrow(#c4ccdd,10px );
}
.arrowNoticearrowOuter:after{
    @include arrowNoticearrow(#ffffff,9px );
    content: "";
    position: absolute;
    top: -9px;
    left: -8px;
}
