@mixin triangleBase($size,$color,$degree){
    display: inline-block;
    position: absolute;
    width: 0px;
    height: 0px;
    //box-sizing: border-box;
    border-top: $size solid transparent;
    border-left: $size solid transparent;
    border-right: $size solid transparent;
    border-bottom: $size solid $color;
    box-shadow: 0 8px 6px -6px rgb(141, 128, 128);

    // -webkit-transition: -webkit-transform 2s 3s ease-in-out;
    transform-origin: center 80%;
    transition: transform 1s 1s ease-in;
   
}
.triangle20Red{
    @include triangleBase(20px,#ff0000 ,0);
}
.movePatternCss{
    transform: translate(0px, 200px);
    -webkit-transform: translate(0px, 200px);
}
.triangle10Green{
    @include triangleBase(25px,#00fb22 ,0);
}
.triangle10Yellow{
    @include triangleBase(30px,#fded05 ,0);
}
@mixin circleBase($rad,$bgcolor,$bordercolor){
    display: inline-block;
    position: absolute;
    width: $rad;
    height: $rad;
    border: 2px solid $bordercolor;
    background-color: $bgcolor;
    border-radius: $rad/2;
    box-shadow: 0 8px 6px -6px rgb(179, 173, 173);
    transform-origin: center 80%;
    transition: transform 1s 1s ease-in;
}
.circle30Red{
    @include circleBase(30px,#ff0000,#ff0000);
}
.circle10Green{
    @include circleBase(30px,#00ff40,#00ff40);
}
.circle10Yellow{
    @include circleBase(30px,#eafa07,#eafa07);
}
.circle10FrameBlue{
    @include circleBase(30px,#ffffff,#1506f3);
}
.circle10FrameRed{
    @include circleBase(30px,#ffffff,#f30e06);
}
