.running {
    -webkit-animation: ball-y 2s;
    -webkit-transform-origin: 50% 50%;
    -moz-animation: ball-y 2s;
    -moz-transform-origin: 50% 50%;
    -webkit-animation-fill-mode: forwards; /* Safari 4.0 - 8.0 */
    animation-fill-mode: forwards;  
}
.fail{
    -webkit-animation: fail-ani 0.5s;
    -webkit-transform-origin: 50% 50%;
    -moz-animation: fail-ani 0.5s;
    -moz-transform-origin: 50% 50%;
    -webkit-animation-fill-mode: forwards; /* Safari 4.0 - 8.0 */
    animation-fill-mode: forwards;  
}
.recover{
    -webkit-animation: recover-ani 0.5s;
    -webkit-transform-origin: 50% 50%;
    -moz-animation: recover-ani 0.5s;
    -moz-transform-origin: 50% 50%;
    -webkit-animation-fill-mode: forwards; /* Safari 4.0 - 8.0 */
    animation-fill-mode: forwards;  
}
@keyframes recover-ani{
    0%{
        -webkit-transform: rotate(90deg);
    }
    100%{
        -webkit-transform: rotate(0deg);
    }
}
@keyframes fail-ani{
    0%{
        -webkit-transform: rotate(0deg);
    }
    100%{
        -webkit-transform: rotate(90deg);
    }
}
@keyframes ball-y{

    0% {
        -webkit-transform: translateY(0px);
        -webkit-animation-timing-function: ease-in;
    }
    40% {
        -webkit-transform: translateY(-100px);
        -webkit-animation-timing-function: ease-in;
    }
    65% {
        -webkit-transform: translateY(-52px);
        -webkit-animation-timing-function: ease-in;
    }
    // 82% {
    //     -webkit-transform: translateY(-25px);
    //     -webkit-animation-timing-function: ease-in;
    // }
    92% {
        -webkit-transform: translateY(-12px);
        -webkit-animation-timing-function: ease-in;
    }
    100% {
        -webkit-transform: translateY(0px);
        -webkit-animation-timing-function: ease-in;
    }
  
}

.colorfulShape {
    animation:myfirst 5s;
    -moz-animation:myfirst 5s infinite; /* Firefox */
    -webkit-animation:myfirst 5s infinite; /* Safari and Chrome */
}

@-moz-keyframes myfirst /* Firefox */ {
    0% {border-color:rgb(154, 238, 20);}
    50% {border-color:rgb(119, 59, 230);}
    100% {border-color:rgb(154, 238, 20);}
}

@-webkit-keyframes myfirst /* Safari and Chrome */ {
    0% {border-color:rgb(226, 94, 17);}
    50% {border-color:rgb(24, 216, 65);}
    100% {border-color:rgb(226, 94, 17);}
}
