$color: rgb(85, 221, 192);

@function makewave($color) {
  $val: 0px 0px $color;
  @for $i from 1 through 100 {
    $val: #{$val}, #{$i*40}px 0px #{$color};
  }
  @return $val;
}

@mixin wave($color) {
  box-shadow: makewave($color);
}



