  background-image: url('../assets/gra2.png');
  background-size: auto 200px;
  background-repeat: no-repeat;
  width: 300px;
  height: 200px;
}

.wave {
  position: absolute;
  left: 0;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  animation-name: wave;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  z-index: inherit;
}
.wave:nth-child(1) {
  top: 0;
  background: $color;
  @include wave($color);
  animation-duration: 20s;
}

.wave:nth-child(2) {
  top: 30px;
  background: lighten($color, 5%);
  @include wave(lighten($color, 5%));
  animation-duration: 18s;
}

.wave:nth-child(3) {
  top: 60px;
  background: lighten($color, 10%);
  @include wave(lighten($color, 10%));
  animation-duration: 16s;
}

.wave:nth-child(4) {
  top: 90px;
  background: lighten($color, 15%);
  @include wave(lighten($color, 15%));
  animation-duration: 14s;
}
.wave:nth-child(5) {
    top: 120px;
    background: lighten($color, 20%);
    @include wave(lighten($color, 20%));
    animation-duration: 12s;
  }
  .wave:nth-child(6) {
    top: 150px;
    background: lighten($color, 25%);
    @include wave(lighten($color, 25%));
    animation-duration: 10s;
  }
@keyframes wave {
  
  0% {
    transform: translate3d(0,0,0);
  }
  100% {
    transform: translate3d(-1000px,0,0);
  }
}
